export const tronRent = {
  data() {
    return {
      available: 73683,
      total: 4249070,
      energy: 100000,
      minAmount: 65000,
      trxAddress: "",
      trxPrice: 0,
      dropdownOpen: true,
      modalVisible: false,
      selectedLanguage: "js",
      energy65Price: 0,
      energy131Price: 0,
      bandwidthPrice: 0,
      activationPrice: 0,
      loading: true,
      walletConnected: false,
    };
  },
  methods: {
    async fetchPrices() {
      try {
        const response = await fetch("https://cryptoswifts.com/api/prices");
        if (!response.ok) throw new Error("Network response was not ok");

        const data = await response.json();

        this.trxPrice = data.trxPrice;
        this.energy65Price = (data.energyPrice / 1000000) * data.wUSDT;
        this.energy131Price = (data.energyPrice / 1000000) * data.woUSDT;
        this.bandwidthPrice = data.bandwidthPrice;
        this.activationPrice = data.activationPrice;
        this.loading = false;

        console.log("Prices updated:", {
          trxPrice: this.trxPrice,
          energy65Price: this.energy65Price,
          energy131Price: this.energy131Price,
          bandwidthPrice: this.bandwidthPrice,
          activationPrice: this.activationPrice,
        });
      } catch (error) {
        console.error("Error fetching prices:", error);
        this.loading = false;
      }
    },
    toggleDropdown() {
      this.dropdownOpen = !this.dropdownOpen;
    },
    openModal() {
      this.modalVisible = true;
      this.selectedLanguage = "js";
    },
    setLanguage(language) {
      this.selectedLanguage = language;
    },
    copyCode() {
      const code =
        this.selectedLanguage === "js"
          ? `const tronweb = require('tronweb');\n\nasync function main() {\n  // JavaScript code goes here\n}\n\nmain();`
          : `from tronpy import TRX, Tron\n\n# Python code goes here`;
      navigator.clipboard.writeText(code).then(() => {
        alert("Code copied to clipboard!");
      });
    },
    closeModal() {
      this.modalVisible = false;
    },

    setEnergy(amount) {
      this.energy = amount;
    },
    async connectWallet() {
      try {
        if (window.tronWeb && window.tronWeb.ready) {
          const walletAddress = window.tronWeb.defaultAddress.base58;
          this.trxAddress = walletAddress;
          this.walletConnected = true;
          alert(`Wallet connected: ${walletAddress}`);
        } else {
          alert(
            "TronLink wallet is not detected. Please install or enable it."
          );
        }
      } catch (error) {
        console.error("Error connecting wallet:", error);
      }
    },

    async getWalletAddress() {
      // Replace this logic with actual wallet connection API or library (e.g., TronLink)
      return "TS3jvZ4djJ9o2GNR9BK2CJfUTRUzTb2X35"; // Example TRX address
    },
  },
  mounted() {
    this.fetchPrices();
    setInterval(() => {
      this.trxPrice = (Math.random() * 0.1 + 0.08).toFixed(4);
    }, 3000);
  },
};
